@import "variables.scss";


* {
  margin: 0;
  padding: 0;
}


body {
  min-width: 320px;

  font-family: $font-family;

  &.overfl {
    overflow: hidden;
    height: 100vh;
  }

  &.bg {
    background: $bg2;

    @media screen and(max-width: 991px) {
      background: $blue;
    }
  }
}

.start-page {
  padding: 30px;

  &-logo {
    display: block;
    margin-bottom: 30px;
  }

  ul {
    display: inline-block;
    vertical-align: top;
    padding-right: 60px;

    li {
      padding: 5px 0;

      b {
        font-size: 20px;
      }

      a {
        font-size: 16px;
        line-height: 140%;
        color: $black;

        &:hover {
          //color: $active;
        }

        .red {
          color: red;
        }
      }
    }
  }
}


div,
span,
a,
input,
textarea,
select,
button,
.button {
  &:focus {
    outline: none;
  }
}

input,
textarea {
  box-shadow: none!important;
}


ul {
  margin: 0;
  padding: 0;

  li {
    list-style: none;
  }
}

a {
  cursor: pointer;
  transition: 0.3s;
  text-decoration: none;

  &:hover {
    text-decoration: none;
  }
}

svg {
  use {
    transition: 0.3s;
  }
}

.container {
  max-width: 100%;

  @media screen and(min-width: 1490px){
    max-width: 1460px;
  }
}


.button {
  background: transparent;
  border: none;
  box-shadow: none;
  outline: none;
  cursor: pointer;
  transition: 0.3s;

  &-primary {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 60px;

    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    color: $white;
    border-radius: 12px;
    background: $blue;

    img {
      margin-right: 10px;
    }

    &:disabled {
      background: $blue4!important;
      cursor: default;
    }

    @media screen and(max-width: 991px){
      height: 55px;
    }

    &:hover {
      background: $blue3;
      color: $white;
    }
  }

  &-secondary {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 60px;

    background: $blue2;
    border-radius: 12px;
    color: $blue3;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;

    &:hover {
      background: $blue;
      color: $white;
    }
  }

  &-link {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 60px;

    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    color: $black2;
    background: transparent;

    &:hover {
      color: $blue3;
    }
  }
}

.logo {
  display: inline-block;

  .enter & {
    margin: 0 auto 0 23.5%;

    @media screen and(max-width: 1575px){
      margin-left: 15%;
    }

    @media screen and(max-width: 1400px){
      margin-left: 5%;
    }

    @media screen and(max-width: 1250px){
      img {
        max-width: 180px;
      }
    }

    @media screen and(max-width: 991px){
      margin: 0 auto;
      display: table;

      img {
        max-width: 171px;
      }
    }
  }

  .header & {
    width: 178px;

    img {
      max-width: 100%;
    }
  }
}

h1 {
  margin-bottom: 20px;

  font-weight: 700;
  font-size: 46px;
  line-height: 135%;
  color: $black2;
  font-family: $font-family2;

  @media screen and(max-width: 1250px){
    font-size: 36px;
  }
}

.enter {
  display: flex;
  height: 100vh;
  min-height: 800px;

  @media screen and(max-width: 991px){
    padding: 84px 20px 160px;
    height: auto;
    min-height: inherit;
    display: block;

    position: relative;

    &:before {
      content: '';
      width: 59px;
      height: 35px;
      display: block;

      position: absolute;
      bottom: 35px;
      left: 57px;

      background-image: url("../images/svg/line-min1.svg");
      background-repeat: no-repeat;
    }
    &:after {
      content: '';
      width: 95px;
      height: 93px;
      display: block;

      position: absolute;
      bottom: 20px;
      right: 32px;

      background-image: url("../images/svg/line-min2.svg");
      background-repeat: no-repeat;
    }
  }

  &__banner {
    width: 54%;
    height: 100%;

    position: relative;
    z-index: 4;

    padding-bottom: 100px;

    @media screen and(max-width: 1250px){
      width: 50%;
    }

    @media screen and(max-width: 991px){
      height: auto;
      width: 100%;
      padding-bottom: 0;
      margin-bottom: 40px;
    }

    &-inner {
      height: 100%;
      padding-top: 100px;

      position: relative;


      &:before {
        content: '';
        width: 102px;
        height: 64px;
        display: block;

        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 2;

        background-image: url("../images/svg/line4.svg");
        background-repeat: no-repeat;

        @media screen and(max-width: 1250px){
          width: 70px;

          background-size: contain;
        }
      }

      &:after {
        content: '';
        width: 175px;
        height: 166px;
        display: block;

        position: absolute;
        bottom: 30px;
        right: -30px;
        z-index: 2;

        background-image: url("../images/svg/line5.svg");
        background-repeat: no-repeat;

        @media screen and(max-width: 1250px){
          width: 100px;
          bottom: 0;

          background-size: contain;
        }
      }

      @media screen and(max-width: 991px){
        height: auto;
        padding-top: 0;

        &:before,
        &:after {
          display: none;
        }
      }
    }

    &-tx {
      height: 20%;
      width: 495px;
      margin: auto;

      position: relative;
      z-index: 5;

      &.tw {
        width: 670px;

        @media screen and(max-width: 1350px){
          width: 505px;

          h1 {
            font-size: 36px;
          }
        }
      }

      @media screen and(max-width: 1050px){
        width: 400px;
      }


      @media screen and(max-width: 991px){
        display: none;
      }

      p {
        margin-bottom: 0;

        font-weight: 500;
        font-size: 20px;
        line-height: 150%;
        color: $black2;
      }
    }

    &-list {
      height: 70%;

      position: relative;

      @media screen and(max-width: 991px){
        height: auto;
        display: none;
      }

      &:after {
        content: '';
        width: 93px;
        height: 50px;
        display: block;

        position: absolute;
        top: -44px;
        right: -51px;
        z-index: 2;

        background-image: url("../images/svg/line2.svg");
        background-repeat: no-repeat;
      }
    }

    &-foto {
      display: inline-block;

      position: absolute;
      z-index: 2;

      &.on {
        top: -102px;
        right: 20%;
        z-index: 4;

        @media screen and(max-width: 1250px){
          img {
            max-width: 200px;
          }
        }

        @media screen and(max-width: 1050px){
          right: 10%;
        }
      }


      &.tw {
        top: 12px;
        left: 0;

        @media screen and(max-width: 1250px){
          top: 50px;

          img {
            max-width: 150px;
          }
        }
      }

      &.th {
        top: 35%;
        left: 23%;

        @media screen and(max-width: 1575px){
          left: 19%;
        }

        @media screen and(max-width: 1400px){
          top: 54%;
          left: 9%;
        }

        @media screen and(max-width: 1250px){
          left: 15%;

          img {
            max-width: 250px;
          }
        }

        @media screen and(max-width: 1050px){
          left: 10%;
        }
      }

      &.fr {
        right: 73px;
        top: 42%;

        @media screen and(max-width: 1575px){
          right: 30px;
        }

        @media screen and(max-width: 1400px){
          top: 43%;
          right: 15px;
        }

        @media screen and(max-width: 1250px){
          img {
            max-width: 180px;
          }
        }
      }

      &-line {
        .on & {
          position: absolute;
          bottom: 59px;
          left: -47px;

          @media screen and(max-width: 1250px){

          }
        }

        .th & {
          position: absolute;

          bottom: -59px;
          left: -119px;

          @media screen and(max-width: 1250px){
            left: -70px;
            bottom: -20px;

            img {
              max-width: 150px;
            }
          }
        }
      }
    }
  }

  &__form {
    width: 46%;

    position: relative;

    @media screen and(max-width: 1250px){
      width: 50%;
    }

    &:after {
      content: '';
      width: 100%;
      height: 100%;
      display: block;

      position: absolute;
      top: 0;
      right: 0;
      z-index: 2;

      background: $bg;
    }


    @media screen and(max-width: 991px){
      width: 100%;

      &:after {
        display: none;
      }
    }


    &-inner {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;

      position: relative;
      z-index: 3;
    }

    &-title {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 30px;

      font-weight: 600;
      font-size: 28px;
      line-height: 150%;
      color: $black2!important;
      text-align: center;

      img {
        margin-right: 13px;

        @media screen and(max-width: 991px){
          display: none;
        }
      }

      @media screen and(max-width: 991px){
        width: 200px;
        margin: 0 auto 25px;

        font-family: $font-family2;
        line-height: 36px;
      }
    }

    &-main {
      width: 517px;
      margin: auto;
      padding: 50px;

      background: $white;
      border-radius: 12px;

      @media screen and(max-width: 1250px){
        width: 400px;
      }

      @media screen and(max-width: 991px){
        max-width: 700px;
        padding: 50px 0;
        width: 100%;
      }

      &-tx {
        max-width: 300px;
        margin: -15px auto 20px;

        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: $black3;
        text-align: center;
      }

      &-tt {
        max-width: 300px;
        margin: 20px auto 0;

        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: $gray;
        text-align: center;

        @media screen and(max-width: 991px){
          margin-top: 10px;
          max-width: 200px;

          font-size: 12px;
          line-height: 150%;
        }
      }
    }
  }
}

.form {
  &__cell {
    margin-bottom: 12px;

    position: relative;

    .toggle-password {
      width: 20px;
      height: 20px;

      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto;
      right: 20px;
      z-index: 2;

      background-image: url("../images/svg/ye.svg");
      background-repeat: no-repeat;
      cursor: pointer;

      &.fa {
        background-image: url("../images/svg/ye2.svg");
      }
    }

    .er {
      display: block;
      margin-top: 4px;

      font-size: 14px;
      line-height: 150%;
      color: $red;
    }

    &.ch {
      position: relative;

      input{
        &[type="checkbox"] {
          display: none;

          & + label {
            display: block;
            padding-left: 25px;

            position: relative;

            font-size: 14px;
            line-height: 150%;
            color: $black2;
            cursor: pointer;

            &:before {
              content: '';
              width: 16px;
              height: 16px;
              display: block;

              position: absolute;
              top: 1px;
              left: 0;
              z-index: 2;

              border-radius: 2px;
              border: 1px solid $blue;
            }
          }

          &:checked {
            & + label {
              &:before {
                background-color: $blue;
                background-image: url("../images/svg/check2.svg");
                background-repeat: no-repeat;
                background-position: center;
              }
            }
          }
        }
      }
    }
  }

  &__bottom {
    margin-top: 20px;

    @media screen and(max-width: 991px){
      margin-top: 16px;
    }

    .button {
      display: flex;
      width: 100%;
      margin-bottom: 10px;

      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
}

.input {
  padding: 17px 24px;
  height: 60px;
  width: 100%;

  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: $black2;

  border: 1px solid $bord;
  box-sizing: border-box;
  border-radius: 12px;

  &::-webkit-input-placeholder { color: $gray; }
  &:-ms-input-placeholder { color: $gray; }
  &::placeholder { color: $gray; }

  .form__cell_error & {
    border-color: $red;
  }

  @media screen and(max-width: 991px){
    height: 56px;

    font-size: 14px;
    line-height: 150%;
  }
}

.textarea {
  padding: 17px 24px 30px;
  height: 129px;
  width: 100%;

  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: $black2;

  border: 1px solid $bord;
  box-sizing: border-box;
  border-radius: 12px;
  resize: none;

  &::-webkit-input-placeholder { color: $gray; }
  &:-ms-input-placeholder { color: $gray; }
  &::placeholder { color: $gray; }

  &-nb {
    font-size: 14px;
    line-height: 150%;
    color: $gray;

    position: absolute;
    bottom: 20px;
    right: 20px;
    z-index: 2;
  }
}

.header {
  margin-bottom: 30px;

  background: $white;

  @media screen and(max-width: 991px) {
    display: none;
  }

  &-mob {
    display: none;

    @media screen and(max-width: 991px) {
      display: block;

      &__user {
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;

        border-radius: 100px;
        background: $white;

        overflow: hidden;
      }
    }
  }

  &__inner {
    padding: 12px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .logo {
    display: inline-block;
    vertical-align: middle;
  }

  &__exit {
    display: inline-flex;
    align-items: center;

    svg {
      width: 24px;
      height: 24px;
      margin-right: 7px;

      use {
        color: $black2;
      }
    }

    span {
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
      color: $black2;
    }
  }

  &__search {
    display: inline-block;
    vertical-align: middle;
    margin-left: 60px;

    @media screen and(max-width: 1250px) {
      .tw & {
        margin-left: 20px;
      }
    }


    &-inner {
      display: flex;
      align-items: center;
      width: 710px;

      @media screen and(max-width: 1450px) {
        .tw & {
          width: 500px;
        }
      }

      @media screen and(max-width: 1250px) {
        .tw & {
          width: 350px;
        }
      }

      @media screen and(max-width: 1200px) {
        width: 540px;
      }

      @media screen and(max-width: 1050px) {
        .tw & {
          width: 300px;
        }
      }
    }

    input {
      width: calc(100% - 52px);
      height: 42px;
      padding: 10px 17px;

      font-size: 14px;
      line-height: 150%;
      border: none;
      background: $bg2;
      border-radius: 6px 0 0 6px;
    }

    .button {
      width: 52px;
      height: 42px;
      display: flex;
      align-items: center;
      justify-content: center;

      background: $blue;
      border-radius: 0 6px 6px 0;

      &:hover {
        background: $blue3;
      }
    }
  }

  &__right {
    .button {
      display: inline-flex;
      height: 42px;
      padding: 11px 19px;

      border-radius: 6px;
    }
  }

  &__user {
    display: inline-flex;
    align-items: center;
    padding-right: 15px;
    margin-left: 30px;

    position: relative;

    cursor: pointer;

    @media screen and(max-width: 1050px) {
      .tw & {
        margin-left: 10px;
      }
    }

    &:after {
      content: '';
      width: 8px;
      height: 15px;
      display: block;

      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
      z-index: 1;

      background-image: url("../images/svg/arrow.svg");
      background-repeat: no-repeat;
      background-size: 8px 15px;
      transform: rotate(-90deg);
      transition: 0.3s;

    }

    &.open {
      &:after {
        transform: rotate(90deg);
      }
    }

    &-foto {
      width: 38px;
      height: 38px;

      border-radius: 100px;
    }

    &-name {
      width: 141px;
      overflow: hidden;

      white-space: nowrap;
      text-overflow: ellipsis;

      padding-left: 12px;

      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
      color: $black2;
    }

    &-top {
      display: inline-flex;
      align-items: center;
    }

    &-drop {
      display: none;
      width: 250px;
      padding: 15px;

      position: absolute;
      top: 55px;
      right: 0;
      z-index: 10;

      background: $white;
      border-radius: 12px;
      border: 1px solid $bord;
      box-sizing: border-box;

      @media screen and(max-width: 991px) {
        top: 91px;
        right: 11px;
      }

      .open & {
        display: block;
      }

      &-item {
        display: block;

        margin-bottom: 10px;
        font-size: 16px;
        line-height: 24px;
        color: $black2;

        &:last-of-type {
          margin-bottom: 0;
        }

        &:hover {
          color: $blue3;
        }
      }
    }

    &-search {
      margin-bottom: 15px;

      &-inner {
        position: relative;

        input {
          height: 45px;
          width: 100%;
          padding: 5px 45px 5px 10px;

          font-size: 16px;
          line-height: 20px;

          border: 1px solid $bord;
          border-radius: 12px;
          background: $white;

          &:focus {
            border-color: $blue;
          }
        }

        .button {

          position: absolute;
          top: 0;
          right: 0;

          background: transparent;
          border: none;
          box-shadow: none;
        }
      }
    }
  }
}

.wrapper {
  margin-bottom: 60px;

  @media screen and(max-width: 991px) {
    padding-top: 40px;
    margin-bottom: 0;
    height: 100vh;
    min-height: 100%;
  }
}

.results {
  @media screen and(max-width: 991px) {
    padding: 23px 0 60px;
    height: 100%;

    background: $white;
    border-radius: 30px 30px 0 0;

    .container,
    .results__inner {
      height: 100%;
    }
  }

  &-mb {
    display: none;

    @media screen and(max-width: 991px) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 15px;
      margin-bottom: 22px;
    }

    &__title {
      margin-bottom: 0;
      color: $white;
      font-size: 24px;
      line-height: 115%;
      font-family: $font-family2;

      a {
        display: none;

        @media screen and(max-width: 991px) {
          display: inline-block;
          vertical-align: middle;
          margin-right: 20px;
        }
      }
    }
  }


  &__inner {
    display: flex;
    align-items: flex-start;

    position: relative;
    z-index: 3;
  }

  &__menu {
    width: 280px;
    padding: 15px 0;

    background: $white;
    border-radius: 6px;

    @media screen and(max-width: 1200px){
      width: 240px;
    }

    @media screen and(max-width: 991px) {
      display: none;
    }

    ul {
      li {
        a {
          display: flex;
          align-items: center;
          padding: 12px 18px;

          span {
            width: calc(100% - 24px);
            padding-left: 10px;

            font-weight: 500;
            font-size: 16px;
            line-height: 130%;
            color: $black2;
            transition: 0.3s;
          }

          svg {
            width: 24px;
            height: 24px;

            position: relative;
            top: -2px;

            use {
              color: $black2;
            }
          }

          &:hover {
            svg use, span {
              color: $blue3;
            }
          }
        }

        &.active {
          a {
            background: $blue2;

            svg use, span {
              color: $blue3;
            }
          }
        }
      }
    }
  }

  &__content {
    width: calc(100% - 280px);
    padding-left: 24px;

    @media screen and(max-width: 1200px){
      width: calc(100% - 240px);
    }

    @media screen and(max-width: 991px) {
      width: 100%;
      padding-left: 0;
    }

    .tw & {
      width: 100%;
      padding-left: 0;
    }

    h1 {
      margin-bottom: 15px;

      font-weight: 600;
      font-size: 28px;
      line-height: 150%;
      font-family: $font-family;

      @media screen and(max-width: 991px) {
        display: none;

        margin-bottom: 22px;
        color: $white;
        font-size: 24px;
        line-height: 115%;
        font-family: $font-family2;
      }
    }
  }

  &__tbl {
    &.top {
      margin-bottom: 12px;

      @media screen and(max-width: 991px) {
        display: none;
      }

      .results__tbl-row {
        cursor: default;

        &:after {
          display: none!important;
        }
      }

      .results__tbl-cell {
        padding: 10px 25px;

        &:first-of-type {
          padding: 10px 0;
        }

        .tw & {
          &:nth-of-type(1) {
            @media screen and(max-width: 991px) {
              display: none;
            }
          }
          &:nth-of-type(2) {
            width: 36%;

            @media screen and(max-width: 1150px) {
              width: 31%;
