            }
          }
          &:nth-of-type(5) {
            width: 11%;

            @media screen and(max-width: 1150px) {
              width: 12%;
            }
          }
          &:nth-of-type(6) {
            width: 11%;

            @media screen and(max-width: 1150px) {
              width: 12%;
            }
          }
          &:nth-of-type(7) {
