      cursor: pointer;
      background: $white;
      border-radius: 6px;

      @media screen and(max-width: 991px) {
        padding: 15px 18px 5px;

        background: $blue5;
        border-radius: 12px;
        flex-wrap: wrap;
        margin-bottom: 10px;

        &:after {
          display: none!important;
        }
      }

      &:after {
        content: '';
        width: 5px;
        height: 100%;
        display: block;

        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;

        background: $blue;
        border-radius: 6px 0 0 6px;
        opacity: 0;
        transition: 0.3s;
      }

      &:hover {
        &:after {
          opacity: 1;
        }
      }
    }

    &-cell {
      padding: 21px 25px;

      @media screen and(max-width: 991px) {
        padding: 0;
      }

      &:nth-of-type(1) {
