        }

        @media screen and(max-width: 1120px){
          width: 43%;
        }

        @media screen and(max-width: 991px) {
          width: 100%;
        }
      }
      &:nth-of-type(3) {
        width: 17%;

        @media screen and(max-width: 991px) {
          width: 50%;
        }
      }
      &:nth-of-type(4) {
        width: 17%;

        @media screen and(max-width: 991px) {
          display: none;
        }
      }
      &:nth-of-type(5) {
        width: 12%;


        .results__tbl-nb {
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }

        @media screen and(max-width: 1350px){
          width: 17%;
        }

        @media screen and(max-width: 1120px){
          width: 19%;
        }

        @media screen and(max-width: 991px) {
          width: 50%;

          text-align: right;
        }
      }

      .tw & {
        &:nth-of-type(1) {
          @media screen and(max-width: 991px) {
            display: none;
          }
        }
        &:nth-of-type(2) {
          width: 38%;

          @media screen and(max-width: 1150px) {
            width: 34%;
          }

          @media screen and(max-width: 991px) {
            width: 100%;
          }
        }
        &:nth-of-type(3) {
          width: 11%;

          @media screen and(max-width: 1150px) {
            width: 12%;
          }

          @media screen and(max-width: 991px) {
            display: none;
          }
        }
        &:nth-of-type(4) {
          width: 11%;

          @media screen and(max-width: 1150px) {
            width: 12%;
          }

          @media screen and(max-width: 991px) {
            width: 50%;
          }
        }
        &:nth-of-type(5) {
          width: 11%;

          @media screen and(max-width: 1150px) {
            width: 12%;
          }

          @media screen and(max-width: 991px) {
            display: none;
          }
        }
        &:nth-of-type(6) {
          width: 11%;

          @media screen and(max-width: 1150px) {
            width: 12%;
          }

          @media screen and(max-width: 991px) {
            width: 50%;
          }
        }
        &:nth-of-type(7) {
          width: 32%;

          text-align: right;

          @media screen and(max-width: 991px) {
            width: 50%;

            text-align: right;
          }
        }
      }
    }

    &-cl {
      margin: 1px 3px;
