      justify-content: center;
      height: 37px;
      padding: 7px 33px;
      margin-top: 2px;

      background: $blue2;
      border-radius: 6px;
      color: $blue3;
      font-weight: 500;
      font-size: 14px;
      line-height: 150%;
      margin-left: 6px;

      &:hover {
        background: $blue;
