      color: $black3;
    }

    &-nb {
      @media screen and(max-width: 991px) {
        color: $gray3;
      }
    }

    &-check {
      width: 16px;
      height: 16px;

      input {
        &[type="checkbox"] {
          display: none;

          & + label {
            display: block;
            padding-left: 18px;

            position: relative;

            font-size: 12px;
            line-height: 130%;
            cursor: pointer;

            &:before {
              content: '';
              width: 16px;
              height: 16px;
              display: block;

              position: absolute;
              top: 1px;
              left: 0;
              z-index: 2;

              border-radius: 2px;
              border: 1px solid $gray2;

              .check & {
                background-image: url("../images/svg/check2.svg");
                background-repeat: no-repeat;
                background-size: 10px 8px;
                background-position: center;
                background-color: $blue;
                border-color: $blue;
              }
            }

          }

          &:checked {
            & + label {
              &:before {
                background-image: url("../images/svg/check2.svg");
                background-repeat: no-repeat;
                background-size: 10px 8px;
                background-position: center;
                background-color: $blue;
                border-color: $blue;
              }
            }
          }
        }
      }
    }

    &-bottom {
      margin-top: 10px;
      display: flex;
      align-items: center;
      justify-content: right;

      &-mob {
        display: none;
        margin-top: 20px  ;

