.pagination {
  display: flex;
  align-items: center;
  justify-content: center;

  &-bl {
    margin-left: 50px;
  }

  .page {
    &-item {
      margin: 0 3px;

      a, span {
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 33px;
        height: 33px;

        font-size: 16px;
        line-height: 150%;
        color: $black2;
        border: none;
        box-shadow: none!important;
        background: transparent!important;

        &:hover {
          color: $blue;
        }
      }

      span {
        cursor: inherit!important;
        color: $black2;
        background: transparent;

        &:hover {
          color: $black2;
        }
      }

      &.active {
        a {
          color: $blue;
        }
      }

      &.prev,
      &.next {
        margin: 0 15px;
        width: 48px;
        height: 48px;

        position: relative;

        &:hover {
          background: transparent;
        }



        .ar {
          width: 48px;
          height: 48px;
          display: flex;
          align-items: center;
          justify-content: center;

          position: absolute;
          top: 0;

          border-radius: 8px!important;
          background: $white!important;
        }

        a {
          position: initial;

          width: 48px;
          height: 48px;

          background: transparent!important;

          &:hover {
            svg {
              use {
                color: $blue;
              }
            }
          }
        }

        svg {
          width: 12px;
          height: 12px;

          use {
            color: $black2;
          }
        }
      }


      &.prev {
        padding-left: 50px;
        margin-left: 0;

        .ar {
          left: 0;
        }

        svg {
         // transform: rotate(90deg);
        }
      }

      &.next {
        padding-right: 50px;
        margin-right: 0;

        .ar {
          right: 0;
        }

        svg {
          transform: rotate(-180deg);
        }
      }
    }
  }
}

